body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5%;
}

.login-panel{
  background-image: linear-gradient(180deg, #3CAFE4 0%, #1E3A6C 100%);
  background-repeat: no-repeat;
  height: 100vh;
  align-items: center;
  text-align: center;
  align-items: center;
  padding-top: 15em;
}

.login-panel p{
  color: #fff;
}

.login-panel img{
  width: 18em;
  text-align: center !important;
}

.btn-active {
  text-align: center !important;
  font-family: Arial;
  font-size: 0.8em;
  background-color: #3cb0e5;
  color: white;
  border-radius: 1.5em;
  border-color: #3cb0e5;
  width: 19.5em;
  height: 3em;
}

.btn-secondary {
  font-family: Arial;
  font-size: 0.8em;
  background-color: #94949400;
  color: #fff;
  border-radius: 1.5em;
  border-color: #fff;
  width: 19.5em;
  height: 3em;
}

.display-flex {
  display: flex;
}
.container-header {
  background-image: linear-gradient(
    to right top,
    #1e3a6c,
    #25558b,
    #2b72a9,
    #3390c7,
    #3cafe4
  );
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}
.container-header div {
  margin-left: 1.5em;
  margin-right: 1.5em;
}
.container-header h1 {
  font-size: 1.5em;
  margin-right: 2em;
  padding-bottom: 1em;
}
.container-header h3 {
  padding-top: 1em;
}
.home {
  width: 100vw;
  height: 30vh;
}


.container-registro{
    background-image: url(/citilarre/static/media/playa.b36c66c9.png);
    background-size: cover;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   
}

.registro{
    display: flex;
    flex-direction: column;
    width: 70vw;

}
.container-registro img{
    width: 18em;
    text-align: center !important;
}
.registro span{
text-align: left;
}
.registro input{
    background-color: rgba(255, 255, 255, 0.5);
    border-style: none;
}
